<template>
  <div class="content">
    <div class="create">
      <span class="title" @click="addRole">新增</span>
    </div>
    <el-table :data="tableData" style="">
      <el-table-column label="编号" width="120" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.date }}</span>
        </template>
      </el-table-column>
      <el-table-column label="角色" width="200" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.role }}</span>
        </template>
      </el-table-column>
      <el-table-column label="备注" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.tip }}</span>
        </template>
      </el-table-column>
      <el-table-column class="btn" width="280" label="操作" align="center">
        <template slot-scope="scope">
          <el-button class="xiu" size="mini" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
          <el-button class="role" size="mini" @click="handleEdit(scope.$index, scope.row)">权限</el-button>
          <el-button class="del" size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="add" v-if="addShow">

      <h3>添加角色</h3>
      <div class="ipt">
        <div class="name item">
          <span>角色名称：</span>
          <input type="text" v-model="rolename">
        </div>
        <div class="tip item">
          <span>备注</span>
          <input type="text" v-model="tip">
        </div>
        <div class="btn">
          <span class="no" @click="cancelAdd">取消</span>
          <span class="yes" @click="sureAdd">添加</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          date: '1',
          tip: '现场数据PC端录入',
          role: '现场'
        }, {
          date: '2',
          tip: '查看现场数据录入',
          role: '处级'
        }, {
          date: '3',
          tip: '查看现场数据录入',
          role: '局级'
        }, {
          date: '4',
          tip: '现场数据PC端录入',
          role: '现场'
        }],
        addShow: false,
        rolename: '',
        tip: ''
      }
    },
    methods: {
      handleEdit(index, row) {
        console.log(index, row);
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      // 新增角色
      addRole() {
        this.addShow = true;
      },

      //  取消添加
      cancelAdd() {
        this.rolename = '',
          this.tip = '',
          this.addShow = false;
      },
      sureAdd() {

      }

    }
  }

</script>

<style lang="scss" scoped>
  .content {
    width: 70%;
    height: 100%;
    padding: 30px;
    padding-bottom: 0;
    position: relative;

    .create {
      text-align: right;
      margin: 10px 0 20px 0;

      .title {
        font-size: 14px;
        color: #fff;
        padding: 5px 26px;
        margin-right: 40px;
        display: inline-block;
        background-color: skyblue;
        border-radius: 4px;
        cursor: pointer;
      }
    }

    .xiu {
      color: #fff;
      background-color: skyblue;
      border: 1px solid yellow;
    }

    .role {
      color: skyblue;
      background-color: #fff;
      border: 1px solid skyblue;
    }

    .del {
      color: red;
      background-color: #fff;
      border: 1px solid red;
    }

    .add {
      width: 60%;
      height: 40%;
      color: #fff;
      background-color: blue;
      border-radius: 8px;
      opacity: 0.9;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
      z-index: 10;
      h3 {
        text-align: center;
        margin: 20px auto;
      }

      .ipt {
        .item {
          display: flex;
          justify-content: center;
          align-items: center;
          margin-bottom: 20px;

          span {
            width: 80px;
            display: inline-block;
          }

          input {
            width: 240px;
            height: 28px;
            padding-left: 6px;
            background-color: rgba(53, 75, 13, 0.39);
            border: 1px solid skyblue;
          }
        }

        .btn {
          margin-top: 40px;
          display: flex;
          justify-content: center;

          span {
            color: #fff;
            font-size: 14px;
            margin: 0 60px;

            &.no {
              padding: 6px 28px;
              border: 1px solid skyblue;
            }

            &.yes {
              padding: 6px 28px;
              background-color: skyblue;
            }
          }
        }
      }
    }
  }

</style>



